.about-me {
  background-image: radial-gradient(
      ellipse,
      #c7b550,
      #ac7510
  );
}

.about-me__header {
  color: white;
  padding: 1rem 2rem;
  font-size: 4rem;
  margin-bottom: 1rem;
}

.about-me__avatar {
  border-radius: 50%;
  width: 100%;
  border: 0.5rem solid rgb(158, 126, 22);
}

.about-me__avatar-wrapper {
  max-width: 50%;
  margin: 1rem auto;
  border: 0.5rem solid rgba(243, 186, 62, 0.3);
  border-radius: 50%;
  position: relative;
  
  &:after {
    content: '';
    position: absolute;
    border: 0.5rem solid rgba(204, 163, 29, 0.3);
    top: -1.5rem;
    bottom: -1.5rem;
    left: -1.5rem;
    right: -1.5rem;
    border-radius: 50%;
  }
}

.about-me__full-name {
  font-size: 2.5rem;
}

.about-me__title {
  font-size: 2rem;
}

.about-me__full-name,
.about-me__title {
  color: white;
  padding: 0.1rem 2rem;
  text-align: center;
}

.about-me__slogan,
.about-me__content {
  font-size: 1.7rem;
  text-align: justify;
  padding: 1rem;
  text-indent: 3rem;
}

.about-me_link-list {
  text-align: center;
}